}
:host-context(body.light-theme) .custom-multiselect .multiselect-dropdown .multiselect-options::-webkit-scrollbar-thumb {
  background: #cbd5e1;
}
:host-context(body.light-theme) .custom-multiselect .multiselect-dropdown .multiselect-options::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}
:host-context(body.light-theme) .custom-multiselect .multiselect-dropdown .multiselect-options .multiselect-option:hover {
  background: rgba(16, 185, 129, 0.03);
}
:host-context(body.light-theme) .custom-multiselect .multiselect-dropdown .multiselect-options .multiselect-option .option-checkbox {
  border-color: #cbd5e1;
}
:host-context(body.light-theme) .custom-multiselect .multiselect-dropdown .multiselect-options .multiselect-option .option-checkbox.checked {
  background: #10B981;
  border-color: #10B981;
}
:host-context(body.light-theme) .custom-multiselect .multiselect-dropdown .multiselect-options .multiselect-option .option-label {
  color: #0f172a;
}
:host-context(body.light-theme) .custom-multiselect.disabled .multiselect-trigger {
  background: #f8fafc;
}
